* {
  padding: 0;
  margin: 0;
}

body {
  background-color: white;
  font-family: Arial, Helvetica, sans-serif;
}

header {
  background-color: white;
  width: 100%;
}

.btn-styles {
  background-color: darkmagenta;
  color: white;
}

.show-controls {
  margin: 0 auto;
  text-align: center;
}

.title-searcher {
  padding: 10px;
}

.show-input {
  margin: 10px;
  padding: 4px;
  height: 24px;
  border-bottom: 2px solid black;
}

#search-show {
  margin: 10px;
  height: 30px;
  padding: 0 14px;
}

.show-result-item {
  display: inline-block;
  margin: 10px;
  text-align: center;
  border: 3px solid palevioletred;
  background-color: lightpink;
  width: 200px;
  height: 300px;
}

.show-result-item-active {
  border: 3px solid purple;
  background-color: violet;
}

.show-result-item img {
  width: 100%;
  height: 100%;
}

li {
  border: 3px solid palevioletred;
  background-color: lightpink;
  margin: 10px;
}
